import React, { useEffect, useState } from 'react';
import Grid from '@mui/material/Grid';
import CircularProgress from '@mui/material/CircularProgress';
import { Button, Fab, TextField } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';

import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';

import { initializeApp } from 'firebase/app';
import {
  collection, getDocs, getFirestore, addDoc,
} from 'firebase/firestore';
import SearchBar from '../../components/Search/Search';

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

function HomePage() {
  // Your web app's Firebase configuration
  const firebaseConfig = {
    apiKey: 'AIzaSyCgBCY9cbnKguyeYGHxWrg8NMX-yN6z8T0',
    authDomain: 'inventarisation-7a1c0.firebaseapp.com',
    databaseURL: 'https://inventarisation-7a1c0-default-rtdb.europe-west1.firebasedatabase.app',
    projectId: 'inventarisation-7a1c0',
    storageBucket: 'inventarisation-7a1c0.appspot.com',
    messagingSenderId: '243472533773',
    appId: '1:243472533773:web:6d885b1a0fbc35d1bfc844',
  };

  // Initialize Firebase
  const app = initializeApp(firebaseConfig);
  const db = getFirestore(app);

  const [initialProducts, setInitialProducts] = useState([]);
  const [filteredProducts, setFilteredProducts] = useState([]);
  const [searchValue, setSearchValue] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const [productToAdd, setProductToAdd] = useState({});
  const [categoriesList, setCategoriesList] = useState([]);

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const fetchProducts = async () => {
    await getDocs(collection(db, 'products'))
      .then((querySnapshot) => {
        const newData = querySnapshot.docs
          .map((doc) => ({ ...doc.data(), id: doc.id }));
        setInitialProducts(newData);
      });
  };

  const fetchCategories = async () => {
    await getDocs(collection(db, 'categories'))
      .then((querySnapshot) => {
        const newData = querySnapshot.docs
          .map((doc) => ({ ...doc.data(), id: doc.id }));
        setCategoriesList(newData);
      });
  };

  const renderProducts = (products) => products.map((item) => {
    const productCategory = categoriesList.find((category) => category?.id === item?.cat_id);
    return (
      <Grid item xs={2} sm={4} md={4} key={item?.id}>
        <div className="grid-item-wrapper">
          <div className="item-name">{item?.name}</div>
          <div className="item-shelf">{item?.shelf}</div>
          <div className="item-category">{productCategory?.cat_name}</div>
          <div className="item-desc">{item?.description}</div>
        </div>
      </Grid>
    );
  });

  const onClickCategory = (category) => {
    const categoryProducts = initialProducts.filter((item) => item?.cat_id === category.cat_id);
    return setFilteredProducts(categoryProducts);
  };

  const renderCategories = (categories) => categories.map((category) => (
    <Grid
      item
      xs={2}
      sm={4}
      md={4}
      onClick={() => onClickCategory(category)}
      key={category?.cat_id}
    >
      <div className="grid-item-wrapper">
        <div className="item-name">{category?.cat_name}</div>
      </div>
    </Grid>
  ));

  const addProduct = async (e) => {
    e.preventDefault();

    try {
      await addDoc(collection(db, 'products'), {
        name: productToAdd?.name,
        description: productToAdd?.description,
        shelf: productToAdd?.shelf,
      });
    } catch (error) {
      console.error('Error adding document: ', error);
    }
  };
  const filterProducts = (searchProductText) => {
    const filteredSearchProducts = initialProducts
      .filter((item) => item?.name.includes(searchProductText));

    setFilteredProducts(filteredSearchProducts);
  };

  useEffect(() => {
    fetchProducts();
    fetchCategories();
    setIsLoading(false);
  }, []);

  useEffect(() => {
    filterProducts(searchValue);
  }, [searchValue]);

  return (
    <div>
      {
            isLoading
              ? (
                <div className="loader">
                  <CircularProgress />
                </div>
              )
              : (
                <div>
                  <SearchBar
                    searchValue={searchValue}
                    setSearchValue={setSearchValue}
                  />
                  <div className="container">
                    <Grid
                      container
                      justifyContent="center"
                      spacing={4}
                      style={{ marginBottom: 20 }}
                    >
                      {renderCategories(categoriesList?.length > 0 ? categoriesList : [])}
                    </Grid>
                    {filteredProducts.length > 0 ? (
                      <Grid
                        container
                        justifyContent="center"
                        spacing={4}
                      >
                        {renderProducts(filteredProducts)}
                      </Grid>
                    ) : null }
                  </div>
                </div>
              )
            }

      <div className="add-button">
        <Fab onClick={handleOpen} color="primary" aria-label="add">
          <AddIcon />
        </Fab>
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>
            <h2>Add new product</h2>
            <div>
              <TextField
                placeholder="Name"
                onChange={(e) => setProductToAdd({ ...productToAdd, name: e.target.value })}
              />
              <TextField
                placeholder="Shelf"
                onChange={(e) => setProductToAdd({ ...productToAdd, shelf: e.target.value })}
              />
              <TextField
                placeholder="Description"
                onChange={(e) => setProductToAdd({ ...productToAdd, description: e.target.value })}
              />
            </div>
            <Button onClick={addProduct}>
              Add product
            </Button>
          </Box>
        </Modal>
      </div>
    </div>
  );
}

export default HomePage;
